@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Poppins', sans-serif;
  background-color: #222;
  color: #ddd;
}

.container {
  text-align: center;
}
.panel-title {
  color: #44d382;
  text-align: left;
  font-size: 20px;
}
.refresh-btn {
  font-size: 20px;
  margin-top: 6px;
  color: #44d382;
  cursor: pointer;
}

header {
  text-align: left;
  color: #44d382;
  padding-top: 10px;
}

.quote-panel {
  text-align: center;
  margin: 10px auto !important;
  display: inline-block;
  font-style: italic;
}
.quote-panel p:first-child {
  font-size: 22px;
  color: #ddd;
}
.quote-panel p:nth-child(2) {
  font-size: 14px;
  text-align: right;
  color: #44d382;
}

.weather-panel {
  /* border: 1px solid #222; */
  display: inline-block;
  padding: 0 !important;
  text-align: center;
}
.weather-item {
  /* border: 1px solid #222; */
  display: inline-block;
  text-align: center;
  padding: 10px;
}
.weather-item p:nth-child(2) { color: #44d382; }

.news-panel {
  /* border: 1px solid #222; */
  display: block;
  /* padding: 0 !important; */
}
.news-item {
  /* border: 1px solid #a00; */
  display: block;
  color: #44d382;
  padding: 10px;
  margin: 10px auto;
  text-align: left;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
}
.news-item p {
  text-decoration: none;
}
.news-meta { color: #ddd; }

footer {
  text-align: center;
  color: #ddd;
}
footer a {
  text-decoration: none;
  color: #44d382;
}
.container {
    /* border: 20px solid #444; */
    width: 100%;
    max-width: 1200px;
    overflow: hidden;
    box-sizing: border-box;
    margin: 0 auto;
    padding: 5px 10px;
}
.row{
    /* border: 20px solid #444; */
    overflow: hidden;
    margin: 20px 0;
}
.col-1 {
    width: 8.33333333%;
    float: left;
    box-sizing: border-box;
    padding: 0 20px;
}
.col-2 {
    width: 16.66666666%;
    float: left;
    box-sizing: border-box;
    padding: 0 20px;
}
.col-3 {
    width: 25%;
    float: left;
    box-sizing: border-box;
    padding: 0 20px;
}
.col-4 {
    width: 100%;
    float: left;
    box-sizing: border-box;
    padding: 0 20px;
}
.col-5 {
    width: 100%;
    float: left;
    box-sizing: border-box;
    padding: 0 20px;
}
.col-6 {
    width: 100%;
    float: left;
    box-sizing: border-box;
    padding: 0 20px;
}
.col-7 {
    width: 58.33333333%;
    float: left;
    box-sizing: border-box;
    padding: 0 20px;
}
.col-8 {
    width: 66.66666666%;
    float: left;
    box-sizing: border-box;
    padding: 0 20px;
}
.col-9 {
    width: 75%;
    float: left;
    box-sizing: border-box;
    padding: 0 20px;
}
.col-10 {
    width: 83.33333333%;
    float: left;
    box-sizing: border-box;
    padding: 0 20px;
}
.col-11 {
    width: 91.66666666%;
    float: left;
    box-sizing: border-box;
    padding: 0 20px;
}
.col-12 {
    width: 100%;
    float: left;
    box-sizing: border-box;
    padding: 0 20px;
}
@media all and (min-width: 768px) {
    .col-4 {
        width: 33.33333333%;
    }
    .col-5 {
        width: 41.66666666%;
    }
    .col-6 {
        width: 50%;
    }
}
